* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

body {
  width: 100%;
  background: #093545;
  overflow-x: hidden;
  overflow-y: auto;
}

.footer_img {
  width: 100%;
  height: 113px;
  object-fit: fill;
}

/* SignIn Page CSS */

.signin_main {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 88vh;
  overflow-y: auto;
}

.signin {
  width: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

.signin_heading {
  font-size: 64px;
  font-weight: 600;
  color: white;
}

.signin_inputField {
  width: 300px;
  border-radius: 10px;
  background: #224957;
  color: white;
  border: none;
  outline: none;
  height: 45px;
  padding: 0px 10px;
}

input::placeholder {
  color: white;
}

.remember {
  display: flex;
  align-items: center;
  gap: 10px;
}

.remember p {
  font-size: 14px;
  color: white;
  font-weight: 400;
}

.remember input[type="checkbox"] {
  height: 18px;
  width: 17px;
  border-radius: 5px;
  background-color: #224957;
  border: none;
  outline: none;
}

.login_btn {
  width: 100%;
  height: 54px;
  background: #2bd17e;
  color: white;
  border-radius: 10px;
  border: none;
  outline: none;
  font-size: 16px;
  font-weight: 700;
  cursor: pointer;
}

/* SignIn Page CSS End */

/* Create Movie Page CSS */

.create_movie_main {
  width: 100%;
  display: flex;
  justify-content: center;
  height: 90vh;
  overflow-y: auto;
}

.create_movie {
  width: 90%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.create_movie h2 {
  font-size: 48px;
  font-weight: 600;
  color: white;
}

.create_movie_upload {
  margin-top: 120px;
  display: grid;
  grid-template-columns: repeat(3, auto);
}

.create_movie_inputField {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.imageWrapper {
  width: 473px;
  height: 504px;
  border-radius: 20px;
  border: 2px dotted white;
  object-fit: contain;
  overflow: hidden;
  cursor: pointer;
}

.imageUpload {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.before_upload {
  color: white;
  font-size: 18px;
  font-weight: 400;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
}

.loader {
  border: 12px solid #f3f3f3;
  border-radius: 50%;
  border-top: 12px solid blue;
  border-right: 12px solid green;
  border-bottom: 12px solid red;
  width: 80px;
  height: 80px;
  -webkit-animation: spin 2s linear infinite;
  animation: spin 2s linear infinite;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.title_inputField {
  width: 362px;
  border-radius: 10px;
  background: #224957;
  color: white;
  border: none;
  outline: none;
  height: 45px;
  padding: 0px 10px;
}

.year_inputField {
  width: 215px;
  border-radius: 10px;
  background: #224957;
  color: white;
  border: none;
  outline: none;
  height: 45px;
  padding: 0px 10px;
}

.submit_btn {
  width: 180px;
  height: 54px;
  background: #2bd17e;
  color: white;
  border-radius: 10px;
  border: none;
  outline: none;
  font-size: 16px;
  font-weight: 700;
  cursor: pointer;
}
.cancel_btn {
  width: 180px;
  height: 54px;
  border: 2px solid white;
  color: white;
  border-radius: 10px;
  outline: none;
  font-size: 16px;
  font-weight: 700;
  cursor: pointer;
  background: transparent;
}

.button_main {
  margin-top: 60px;
  display: flex;
  gap: 20px;
}

.ant-pagination a {
  background: #092c39;
  color: white !important;
  border: none;
  border-radius: 5px;
  width: 32px;
  height: 32px;
}

.ant-pagination .ant-pagination-item-active a {
  color: white !important;
  background: green;
  border: none !important;
  width: 32px;
  height: 32px;
}

.ant-pagination .ant-pagination-item-active {
  background: none;
  border: none !important;
}

.prev_btn {
  background: transparent !important;
  font-size: 16px;
  font-weight: 700;
  margin-right: 10px;
}

.next_btn {
  background: transparent !important;
  font-size: 16px;
  font-weight: 700;
  margin-left: 10px;
}

@media only screen and (max-width: 1000px) {
  .imageWrapper {
    width: 300px;
    height: 300px;
    border-radius: 20px;
    border: 2px dotted white;
    object-fit: contain;
    overflow: hidden;
    cursor: pointer;
  }
  .create_movie_upload {
    display: grid;
    grid-template-columns: repeat(2, auto);
  }
}

@media only screen and (max-width: 850px) {
  .create_movie_upload {
    grid-template-columns: repeat(1, auto);
    justify-content: center;
    margin-top: 70px;
  }
  .create_movie h2 {
    text-align: center;
    font-size: 38px;
  }
  .create_movie_inputField {
    align-items: center;
    margin-top: 30px;
  }
  .title_inputField {
    width: 362px;
  }
  .year_inputField {
    width: 362px;
  }
  .ant-upload-wrapper {
    display: flex;
    justify-content: center;
  }
}

@media only screen and (max-width: 700px) {
  .create_movie h2 {
    font-size: 28px;
  }
}

/* Create Movie Page CSS End*/

/* My Movies Page CSS */

.movies_main {
  width: 100%;
  height: 100%;
  overflow-y: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 80px;
  margin-bottom: 40px;
}

.movie_empty {
  width: 100%;
  height: 88vh;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 80px;
  margin-bottom: 40px;
}

.movie {
  width: 90%;
  height: 100%;
  display: flex;
  flex-direction: column;
  color: white;
}

.movie_header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.plus_icon {
  margin-left: 10px;
  cursor: pointer;
}

.logout_icon {
  margin-left: 20px;
  transform: rotate(90deg);
}

.movies_cards {
  display: grid;
  grid-template-columns: repeat(4, auto);
  gap: 20px;
  margin-top: 120px;
}

.movie_card {
  width: 100%;
  height: 504px;
  background: #092c39;
  padding: 10px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.movie_card img {
  width: 100%;
  height: 80%;
  border-radius: 10px;
}

.movie_name {
  font-size: 20px;
  font-weight: 500;
  color: white;
}

.movie_year {
  font-size: 14px;
  font-weight: 400;
  color: white;
}

.emptyList {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 20px;
  min-height: 70vh;
}

.emptyList h2 {
  font-size: 48px;
  font-weight: 600;
  color: white;
}

.emptyList button {
  width: 200px;
  height: 56px;
  background: #2bd17e;
  color: white;
  border: none;
  outline: none;
  font-weight: 700;
  border-radius: 10px;
}

.pagination {
  display: flex;
  justify-content: center;
  margin: 40px 0px;
}

.logout_btn {
  background: none;
  border: none;
  outline: none;
  font-size: 16px;
  color: white;
  font-weight: 700;
  cursor: pointer;
}

@media only screen and (max-width: 1150px) {
  .movies_cards {
    grid-template-columns: repeat(3, auto);
  }
}

@media only screen and (max-width: 850px) {
  .movies_cards {
    grid-template-columns: repeat(2, auto);
  }
}

@media only screen and (max-width: 500px) {
  .movies_cards {
    grid-template-columns: repeat(1, auto);
  }
}

@media only screen and (max-width: 650px){
  .emptyList h2 {
    font-size: 32px;
  }
}
@media only screen and (max-width: 405px){
  .emptyList h2 {
    font-size: 24px;
  }
  .emptyList button {
    width: 170px;
    height: 50px;
  }
}

/* My Movies Page CSS End*/
